// MultiStage Loading Component
const MultiStageLoading = ({ stage }) => {
  const getStageContent = () => {
    switch (stage) {
      case 'profile':
        return {
          icon: <User className="w-8 h-8 text-blue-400 animate-pulse" />,
          title: 'Loading Profile...',
          description: 'Fetching your profile information'
        };
      case 'payments':
        return {
          icon: <DollarSign className="w-8 h-8 text-green-400 animate-pulse" />,
          title: 'Loading Payment Status...',
          description: 'Checking your payment history'
        };
      case 'complete':
        return {
          icon: <CheckCircle className="w-8 h-8 text-orange-400 animate-bounce" />,
          title: 'Almost Done...',
          description: 'Finalizing data loading'
        };
      case 'error':
        return {
          icon: <AlertCircle className="w-8 h-8 text-red-400" />,
          title: 'Error Loading Data',
          description: 'Please refresh the page and try again'
        };
      default:
        return {
          icon: <Loader className="w-8 h-8 text-orange-400 animate-spin" />,
          title: 'Initializing...',
          description: 'Preparing to load your data'
        };
    }
  };

  const content = getStageContent();

  return (
    <div className="flex flex-col items-center justify-center p-12 space-y-4">
      <div className="relative">
        {content.icon}
        <div className="absolute inset-0 rounded-full bg-blue-400/20 animate-ping"></div>
      </div>
      <div className="text-center">
        <h3 className="text-lg font-semibold text-white mb-2">{content.title}</h3>
        <p className="text-sm text-slate-400">{content.description}</p>

        {/* Progress indicators */}
        <div className="flex items-center justify-center space-x-2 mt-4">
          <div className={`w-2 h-2 rounded-full transition-colors duration-300 ${stage === 'profile' || stage === 'payments' || stage === 'complete' ? 'bg-blue-400' : 'bg-slate-600'
            }`}></div>
          <div className={`w-2 h-2 rounded-full transition-colors duration-300 ${stage === 'payments' || stage === 'complete' ? 'bg-blue-400' : 'bg-slate-600'
            }`}></div>
          <div className={`w-2 h-2 rounded-full transition-colors duration-300 ${stage === 'complete' ? 'bg-blue-400' : 'bg-slate-600'
            }`}></div>
        </div>
      </div>
    </div>
  );
};

