import { API_URL } from '../config/api';

class CustomerPasswordService {
  // Verify current password
  static async verifyCurrentPassword(customerId, currentPassword) {
    try {
      const response = await fetch(`${API_URL}/customer-password-reset/verify-current-password`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          customerId: customerId.trim().toUpperCase(),
          currentPassword: currentPassword.trim()
        })
      });

      const data = await response.json();

      if (!response.ok || !data.success) {
        throw new Error(data.message || 'Password verification failed');
      }

      return data;
    } catch (error) {
      console.error('Password verification error:', error);
      throw error;
    }
  }

  // Update password
  static async updatePassword(customerId, currentPassword, newPassword) {
    try {
      const response = await fetch(`${API_URL}/customer-password-reset/update-password`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          customerId: customerId.trim().toUpperCase(),
          currentPassword: currentPassword.trim(),
          newPassword: newPassword.trim()
        })
      });

      const data = await response.json();

      if (!response.ok || !data.success) {
        throw new Error(data.message || 'Password update failed');
      }

      return data;
    } catch (error) {
      console.error('Password update error:', error);
      throw error;
    }
  }

  // Validate password strength locally for immediate feedback
  static validatePasswordStrength(password) {
    const minLength = password.length >= 8;
    const hasUppercase = /[A-Z]/.test(password);
    const hasLowercase = /[a-z]/.test(password);
    const hasNumber = /\d/.test(password);
    const hasSpecialChar = /[@$!%*?&]/.test(password);

    const errors = [];
    if (!minLength) errors.push('At least 8 characters');
    if (!hasUppercase) errors.push('1 uppercase letter');
    if (!hasLowercase) errors.push('1 lowercase letter');
    if (!hasNumber) errors.push('1 number');
    if (!hasSpecialChar) errors.push('1 special character (@$!%*&)');

    return {
      isValid: minLength && hasUppercase && hasLowercase && hasNumber && hasSpecialChar,
      errors: errors,
      strength: this.calculatePasswordStrength(password)
    };
  }

  // Calculate password strength (0-100)
  static calculatePasswordStrength(password) {
    let strength = 0;

    // Length contribution
    if (password.length >= 8) strength += 20;
    if (password.length >= 12) strength += 10;

    // Character variety contribution
    if (/[a-z]/.test(password)) strength += 15;
    if (/[A-Z]/.test(password)) strength += 15;
    if (/\d/.test(password)) strength += 15;
    if (/[@$!%*?&]/.test(password)) strength += 15;
    if (/[^a-zA-Z0-9@$!%*?&]/.test(password)) strength += 10; // Other special chars

    return Math.min(strength, 100);
  }

  // Get strength color based on score
  static getStrengthColor(strength) {
    if (strength < 30) return 'text-red-500';
    if (strength < 50) return 'text-orange-500';
    if (strength < 70) return 'text-yellow-500';
    if (strength < 90) return 'text-green-500';
    return 'text-green-600';
  }

  // Get strength text based on score
  static getStrengthText(strength) {
    if (strength < 30) return 'Very Weak';
    if (strength < 50) return 'Weak';
    if (strength < 70) return 'Fair';
    if (strength < 90) return 'Good';
    return 'Strong';
  }
}

export default CustomerPasswordService;
